import Tasks from "./tasks";

const Project = () => {
  const projectList = [];

  const getProjectList = () => projectList;

  const createSingleProject = () => {
    const tasks = Tasks();
    const storeProjectName = prompt("project name?");
    const singleProject = Object.assign({});
    singleProject[storeProjectName] = tasks.createTask();
    tasks.addTask(singleProject[storeProjectName]);
    console.log(singleProject);
    return singleProject;
  };

  const addSingleProjectToList = (project) => {
    projectList.push(project);
  };

  return { getProjectList, createSingleProject, addSingleProjectToList };
};

export default Project
